<template>
  <div class="app">
    <keep-alive>
      <router-view :seller="seller"></router-view>
    </keep-alive>
  </div>
</template>

<script type="text/ecmascript-6">
import {urlParse} from './common/js/util';
export default {
  name: 'app',
  data () {
    return {
      seller: {
        id: (() => {
          let queryParam = urlParse();
          return queryParam.id;
        })()
      },
      isLogin: false,
      userInfo: { // 保存用户信息
        nick: null,
        ulevel: null,
        uid: null,
        portrait: null
      }
    };
  },
  components: {
  },
  mounted () {
    // 组件开始挂载时获取用户信息
    this.getUserInfo();
  },
  methods: {
    // 请求用户的一些信息
    getUserInfo() {
      // 发送http请求获取，这里写死作演示
      this.userInfo = {
        nick: 'Doterlin',
        ulevel: 20,
        uid: '10000',
        portrait: 'images/profile.png'
      };
      this.$store.commit('updateUserInfo', this.userInfo);
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "./common/stylus/mixin.styl"
  .app
    .tab
      display: flex
      width: 100%
      height: 40px
      line-height: 40px
      border-bottom-1px(rgba(7,17,27,0.1))
      .tab-item
        flex: 1
        text-align: center
        & > a
          display: block
          font-size: 14px
          color: rgb(77,85,93)
          &.active
            color: rgb(240,20,20)
</style>



