<template>
  <div>
    <div class="tab border-bottom-1px">
      <div class="tab-item">
        <router-link to="/goods">订货下单</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/ratings">我的账户</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/seller">最新信息</router-link>
      </div>
    </div>
    <div class="ratings" ref="ratings">
      <div class="ratings-content">
        <div class="overview">
          <div class="overview-left">
            <h1 class="score">月结客户</h1>
            <div class="title">用户结款评级： 5.0</div>
            <star :size="36" :score="5.0"></star>
          </div>
          <div class="overview-right">
            <div class="score-wrapper">
              <span class="title">未结金额：</span>
              <!--<star :size="36" :score="seller.serviceScore"></star>-->
              <span class="score">15687元</span>
            </div>
            <div class="score-wrapper">
              <span class="title">未结定单：</span>
              <!--<star :size="36" :score="seller.foodScore"></star>-->
              <span class="score">4单</span>
            </div>
            <div class="score-wrapper">
              <span class="title">累计消费：</span>
              <!--<star :size="36" :score="seller.serviceScore"></star>-->
              <span class="score">159871元</span>
            </div>
            <!--<div class="delivery-wrapper">
              <span class="title">送达时间</span>
              <span class="delivery">{{seller.deliveryTime}}分钟</span>
            </div>-->
          </div>
        </div>
        <split></split>
        <!--<ratingselect @select="selectRating" @toggle="toggleContent" :selectType="selectType" :onlyContent="onlyContent"
                      :ratings="ratings"></ratingselect>-->
        <div class="ordertable">
          <div class="ordertitle">
            <span class="th">订单编号</span>
            <span class="th">订单日期</span>
            <span class="th">金额</span>
            <span class="th">订单状态</span>
          </div>
          <div class="ordertr" v-for="item in orders">
            <span class="td">{{ item.increment_id }}</span>
            <span class="td">{{ item.created_at }}</span>
            <span class="td">{{ item.base_subtotal }}元</span>
            <span class="td">{{ item.status }}</span>
          </div>
        </div>
        <!--<div class="rating-wrapper">
          <ul>
            <li v-for="rating in ratings" v-show="needShow(rating.rateType, rating.text)" class="rating-item">
              <div class="avatar">
                <img width="28" height="28" :src="rating.avatar">
              </div>
              <div class="content">
                <h1 class="name">{{rating.username}}</h1>
                <div class="star-wrapper">
                  <star :size="24" :score="rating.score"></star>
                  <span class="delivery" v-show="rating.deliveryTime">{{rating.deliveryTime}}</span>
                </div>
                <p class="text">{{rating.text}}</p>
                <div class="recommend" v-show="rating.recommend && rating.recommend.length">
                  <span class="icon-thumb_up"></span>
                  <span class="item" v-for="item in rating.recommend">{{item}}</span>
                </div>
                <div class="time">
                  {{rating.rateTime | formatDate}}
                </div>
              </div>
            </li>
          </ul>
        </div>-->
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll';
  import split from './../split/split';
  import ratingselect from './../ratingselect/ratingselect';
  import {formatDate} from './../../common/js/date.js';
  import star from './../star/star';

  const ALL = 2;
  const ERR_OK = 0;

  export default {
    props: {
      seller: {
        type: Object
      }
    },
    data() {
      return {
        orders: [],
        ratings: [],
        selectType: ALL,
        onlyContent: true
      };
    },
    created() {
      this.$http.get('/api/ratings').then((response) => {
        response = response.body;
        console.log(response);
        if (response.errno === ERR_OK) {
          this.ratings = response.data;
          this.$nextTick(() => {
            this.scroll = new BScroll(this.$refs.ratings, {
              click: true
            });
          });
        }
      });
      this.$http.get('http://xb.04wu.com/test.php').then((response) => {
        response = response.body;
        console.log(response);
        if (ERR_OK === 0) {
          this.orders = response.items;
        }
        console.log(this.orders);
      });
    },
    methods: {
      needShow(type, text) {
        if (this.onlyContent && !text) {
          return false;
        }
        if (this.selectType === ALL) {
          return true;
        } else {
          return type === this.selectType;
        }
      },
      selectRating(type) {
        this.selectType = type;
        this.$nextTick(() => {
          this.scroll.refresh();
        });
      },
      toggleContent() {
        this.onlyContent = !this.onlyContent;
        this.$nextTick(() => {
          this.scroll.refresh();
        });
      }
    },
    filters: {
      formatDate(time) {
        let date = new Date(time);
        return formatDate(date, 'yyyy-MM-dd hh:mm');
      }
    },
    components: {
      star,
      split,
      ratingselect
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl"
  .ratings
    position: absolute
    top: 40px
    bottom: 0
    left: 0
    width: 100%
    overflow: hidden
    .ordertable
      padding 18px 18px 0;
    .ordertitle
      display:flex;
      width:100%
      .th
        display:block;
        width:25%
        padding:10px;
        background-color: rgb(128, 102, 160);
        color: #fff;
        border-bottom-width: 0;
        text-align:center
        font-size: 12px;
        font-weight: bold
    .ordertr
      display:flex;
      width:100%
      border-width: 0 0 1px 0
      border-style: solid;
      border-color: rgb(128, 102, 160);
      .td
        display:block;
        width:25%
        padding:10px;
        text-align:center
        font-size: 12px;border-width: 1px;
    .overview
      display: flex
      padding: 18px 0
      .overview-left
        flex: 0 0 137px
        padding: 6px 0
        width: 137px
        border-right: 1px solid rgba(7, 17, 27, 0.1)
        text-align: center
        @media only screen and (max-width: 320px)
          flex: 0 0 120px
          width: 120px
        .score
          margin-bottom: 6px
          line-height: 28px
          font-size: 24px
          color: rgb(255, 153, 0)
        .title
          margin-bottom: 8px
          line-height: 12px
          font-size: 12px
          color: rgb(7, 17, 27)
        .rank
          line-height: 10px
          font-size: 10px
          color: rgb(147, 153, 159)
      .overview-right
        flex: 1
        padding: 6px 0 6px 24px
        @media only screen and (max-width: 320px)
          padding-left: 6px
        .score-wrapper
          margin-bottom: 8px
          font-size: 0
          .title
            display: inline-block
            line-height: 18px
            vertical-align: top
            font-size: 12px
            color: rgb(7, 17, 27)
          .star
            display: inline-block
            margin: 0 12px
            vertical-align: top
          .score
            display: inline-block
            line-height: 18px
            vertical-align: top
            font-size: 12px
            color: rgb(255, 153, 0)
        .delivery-wrapper
          font-size: 0
          .title
            line-height: 18px
            font-size: 12px
            color: rgb(7, 17, 27)
          .delivery
            margin-left: 12px
            font-size: 12px
            color: rgb(147, 153, 159)
    .rating-wrapper
      padding: 0 18px
      .rating-item
        display: flex
        padding: 18px 0
        border-bottom-1px(rgba(7, 17, 27, 0.1))
        .avatar
          flex: 0 0 28px
          width: 28px
          margin-right: 12px
          img
            border-radius: 50%
        .content
          position: relative
          flex: 1
          .name
            margin-bottom: 4px
            line-height: 12px
            font-size: 10px
            color: rgb(7, 17, 27)
          .star-wrapper
            margin-bottom: 6px
            font-size: 0
            .star
              display: inline-block
              margin-right: 6px
              vertical-align: top
            .delivery
              display: inline-block
              vertical-align: top
              line-height: 12px
              font-size: 10px
              color: rgb(147, 153, 159)
          .text
            margin-bottom: 8px
            line-height: 18px
            color: rgb(7, 17, 27)
            font-size: 12px
          .recommend
            line-height: 16px
            font-size: 0
            .icon-thumb_up, .item
              display: inline-block
              margin: 0 8px 4px 0
              font-size: 9px
            .icon-thumb_up
              color: rgb(0, 160, 220)
            .item
              padding: 0 6px
              border: 1px solid rgba(7, 17, 27, 0.1)
              border-radius: 1px
              color: rgb(147, 153, 159)
              background: #fff
          .time
            position: absolute
            top: 0
            right: 0
            line-height: 12px
            font-size: 10px
            color: rgb(147, 153, 159)
</style>



